import React, { PureComponent } from 'react';
import '../css/Person.css';
const random = require('random');
let interval = null;

export class Person extends PureComponent {

    constructor(props) {
        super(props);

        this.state = {
            pers : null
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        console.log('update');
    }

    componentWillUnmount() {
        console.log('unmount');
        if(interval !== null){
            clearInterval(interval);
        }
    }

    componentDidMount() {
        let user = this.props.user;
        let persons = this.props.persons;
        let i = this.props.person;
        let skin = this.props.hasOwnProperty('skin') ?
            persons[i].skins[this.props.skin].url
            :
            user.hasOwnProperty('active_skins') ?
                user.active_skins.hasOwnProperty(i+'') ?
                    user.active_skins[i] === null ?
                        persons[i].skins[0].url
                        :
                        persons[i].skins[user.active_skins[i]].url
                    :
                    persons[i].skins[0].url
                :
                persons[i].skins[0].url;

        if(this.props.is_game){
            let transform = random.int(-40, 40);
            interval = setInterval(()=>{
                let chance = random.float(0,1);
                if(chance<=0.15){
                    if(i === 1){
                        transform = -transform;
                        this.setState({ pers:
                                <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                    <img className={this.props.className} src={skin} onClick={this.props.onClick} style={{ height: '170px', transform: 'translate('+transform+'%)' }}/>
                                </div>
                        });
                    }
                }
            },500);
        }

        this.setState({ pers:
                <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                    <img className={this.props.className} src={skin} onClick={this.props.onClick} style={{ height: '170px' }}/>
                </div>
        });
    }

    render() {
        return (
            this.state.pers
        )
    }

}